@import './../../App.scss';  //import vars and mixins
.Media{
    background: #FAFAFA;

    .section__content{
        display: grid;
        grid-gap: 0;
        grid-template-columns: 1fr 1fr 1fr;
        grid-auto-rows: 20rem;
    }
    .media{
        div{
            padding: 3rem;
            color: #fff;
        }
        background: $color_theme;
        text-align: center;
        margin: .2rem;
        overflow: hidden;
        font-size: 1.3rem;
        b{
            font-weight: 600;
            padding-bottom: .8rem;
            font-size: 1.4rem;
            line-height: 1.2;
        }
        small{
            position: absolute;
            bottom: 2rem;
            width: 100%;
            padding: 0 1rem;
            font-size: 1rem;
        }
    }
    .media__bg{
        background-size: cover;
        background-position: center;
        opacity: .3;
        transition: .5s;
    }

    .media__play{
        img{
            width: 45%;
            margin: auto;
            cursor: pointer;
        }
        top: 100%;
    }
    .media:hover{
        .media__bg{
            opacity: .8;
            transition: .7s;
            transform: scale(1.2);
        }
        .media__text{
            top: -50%;
            opacity: 0;
            transition: .5s;
        }
        .media__play{
            top: 0;
            transition: .5s ease-in-out;
        }
    }

}
.player{
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(5,5,5,.9);
    z-index: 99;
    height: 100vh;
    width: 100%;
    display: grid;
    align-content: center;
    text-align: center;
    iframe{
        margin: auto;
    }
    .close{
        color: #fff;
        font-size: 6rem;
        position: absolute;
        right: 12rem;
        top: 6rem;
        cursor: pointer;
    }
    top: 80%;
    opacity: 0;
    @include anim(SlidePlayer, .25s, 0s, ease-out);
}
